.toolbar {
  width: 100%;
  position: fixed;
  height: 32px;
  background-color: white;
  border-bottom: 1px solid #181a1f;
  z-index: 100;
  display: flex;
  align-items: center;
  transition: background-color 350ms, color 350ms; }
  .toolbar .toggle-panel-icon {
    display: inline-block;
    width: 24px;
    height: 24px;
    cursor: pointer;
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    margin: auto 3px auto;
    text-align: center; }
    .toolbar .toggle-panel-icon.icon {
      -webkit-mask-size: cover;
      mask-size: cover;
      -webkit-marsk-size: cover;
      transition: background-color 350ms;
      background-color: black; }
  .toolbar .icon-half {
    -webkit-mask: url("../../images/svg/half.svg") 50% 50% no-repeat; }
  .toolbar .icon-rhalf {
    -webkit-mask: url("../../images/svg/rhalf.svg") 50% 50% no-repeat; }
  .toolbar .icon-lhalf {
    -webkit-mask: url("../../images/svg/lhalf.svg") 50% 50% no-repeat; }
  .toolbar .icon-darkmode {
    -webkit-mask: url("../../images/feather/moon.svg") 50% 50% no-repeat; }
  .toolbar .h1, .toolbar .h2, .toolbar .h3, .toolbar .h4, .toolbar .h5, .toolbar .h6 {
    font-weight: bold;
    display: flex;
    align-items: center; }

.dark .toolbar {
  background-color: #21252c;
  color: #abb2bf; }
  .dark .toolbar .toggle-panel-icon.icon {
    background-color: #abb2bf; }
